import { Component, OnInit } from '@angular/core';
import { ActionSheetController } from '@ionic/angular';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-active',
  templateUrl: './active.page.html',
  styleUrls: ['./active.page.scss'],
})
export class ActivePage implements OnInit {
  cases: any = [
    {
      type: 'Office',
      subType: 'timesheet',
      callerId: '#0001',
      callerName: 'Michael Basalan',
      message: `Caller Information
         <br> &nbsp; &nbsp; &nbsp; Caller Type: Patient
          <br> &nbsp; &nbsp; &nbsp; First name: Michael Basalan
         `,
      status: 'Active',
      statusColor: 'secondary',
      callStatus: 'Accepted',
      callStatusColor: 'light',
      lastAssignee: 'Paul Lieberman',
      read: true,
      from: 'CuraCall Message'
    },
    {
      type: 'Building',
      subType: 'timestamp',
      callerId: '#0001',
      callerName: 'John Lennon',
      message: `Caller Information
         <br> &nbsp; &nbsp; &nbsp; Caller Type: Patient
          <br> &nbsp; &nbsp; &nbsp; First name: Michael Basalan
         `,
      status: 'Active',
      statusColor: 'secondary',
      callStatus: 'Accepted',
      callStatusColor: 'light',
      lastAssignee: 'Paul Lieberman',
      read: false,
      from: 'CuraCall Message'
    },
    {
      type: 'House',
      subType: 'window',
      callerId: '#0001',
      callerName: 'Vijay Kumar',
      message: `Caller Information
         <br> &nbsp; &nbsp; &nbsp; Caller Type: Patient
          <br> &nbsp; &nbsp; &nbsp; First name: Michael Basalan
         `,
      status: 'Active',
      statusColor: 'secondary',
      callStatus: 'Accepted',
      callStatusColor: 'light',
      lastAssignee: 'Paul Lieberman',
      read: false,
      from: 'CuraCall Message'
    },
  ];

  constructor(
    public actionSheetController: ActionSheetController,
    private router: Router,
    private route: ActivatedRoute,
    ) {}

  ngOnInit() {
  }

  async presentActionSheet() {
    const actionSheet = await this.actionSheetController.create({
      header: 'Filter',
      buttons: [{
        text: 'All Cases (99)',
        handler: () => {
          console.log('All cases clicked');
          this.router.navigate(['/tabs/tab1'], { replaceUrl: true });
        }
      }, {
        text: 'Active Cases (33)',
        handler: () => {
          console.log('Active cases clicked');
          this.router.navigate(['/tabs/tab1/case/active'], { replaceUrl: true });

        }
      }, {
        text: 'Pending Cases (33)',
        handler: () => {
          console.log('Pending clicked');
          this.router.navigate(['/tabs/tab1/case/pending'], { replaceUrl: true });
        }
      }, {
        text: 'Closed Cases (33)',
        handler: () => {
          console.log('Closed Cases clicked');
          this.router.navigate(['/tabs/tab1/case/closed'], { replaceUrl: true });
        }
      }, {
        text: 'Cancel',
        role: 'cancel',
        handler: () => {
          console.log('Cancel clicked');
        }
      }]
    });
    await actionSheet.present();
  }

}
